<template>
  <div>

    <el-table
        :data="newsList"
        height="800"
        style="width: 100%">
      <el-table-column
          label="新闻标题"
          width="1000"
          align="center">
        <template slot-scope="scope">
          <a :href="scope.row.link"  target="_blank" style="text-decoration: none"><el-tag size="medium">{{ scope.row.title }}</el-tag></a>
        </template>
      </el-table-column>
      <el-table-column
          label="操作"
          width="500"
          align="center">
        <template slot-scope="scope">
          <el-button
              size="mini"
              @click="toShowNews(scope.$index, scope.row)">去看新闻
          </el-button>
        </template>
      </el-table-column>
    </el-table>

  </div>
</template>


<script>
export default {
  name: "CnWeiBoHotSearch",
  data() {
    return {
      newsList:[]
    }
  },

  mounted() {

  },

  created() {
    window.document.title = "微博热搜榜"
    this.requestNewsList()
  },

  methods: {

    requestNewsList(){
      this.chart.get("/news/cnWeiBoHotSearch")
          .then(resp =>{
            if (resp.data.code > 10000 && resp.data.code < 20000){
              console.log(resp)
              this.$message.success(resp.data.msg)
              this.newsList = resp.data.result_list
            }else {
              this.$message.error(resp.data.msg)
            }
          })
    },



    toShowNews(index, data) {
      location.href = data.link
    },

  }


}
</script>

<style scoped>

</style>
